import axios from 'axios';
import moment from 'moment';
import { Container, Accordion } from "react-bootstrap";
import { useContext, useEffect, useState } from 'react';
import Select from "react-select";
import { useHistory, useParams } from 'react-router'
import { Context } from '../context/Context'
import Swal from 'sweetalert2';
import { Link } from "react-router-dom";

// import "./privileages.css"
const UserPrivileage = ()=>{
    const { user, dispatch } = useContext(Context)
  const { id } = useParams()
  const [evangelist, setEvangelist] = useState(null)
  const [error, setError] = useState(null)
  const [isLoading, setIsLoading] = useState(true)
  const history = useHistory();

  useEffect(()=>{
    const config = {
        headers: { Authorization: `Bearer ${user.jwt}` },
      }
  
      axios
        .get(`https://evangelism.thecophq.org/api/users/${id}`, config)
        .then((result) => {
          setEvangelist(result.data)
        //   console.log(result.data);
          setError(null)
          setIsLoading(false)
        })
        .catch((err) => {
          // console.log("an error occured");
          setError('An error has been encountered, please try again later')
          setIsLoading(false)
        });
  },[id])
  
  const handlePrivilege = ()=>{
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios.put(`https://evangelism.thecophq.org/api/users/${id}`,{bolcked:false},config)
          .then((result)=>{
            Swal.fire({
              title: 'Suucessful',
              text: `Evangelist privilege has been granted to ${result.data.username}`,
              icon: 'success',
              // timer:2000
            }).then(response=>{
              // console.log(response);
              if(response.isConfirmed){
                  history.goBack();
              }
            })
          })
          .catch((error)=>{
            Swal.fire({
              title: 'Error',
              text: 'An error occurred, please try again latter',
              icon: 'error',
              showCancelButton:false,
          });
          })
  }

  const handleDeleteEvangelist = ()=>{
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    }
    axios.delete(`https://evangelism.thecophq.org/api/users/${id}`,config)
          .then((result)=>{
            Swal.fire({
              title: 'Suucessful',
              text: `${result.username} deleted successfully`,
              icon: 'success',
              // timer:2000
            }).then(response=>{
              // console.log(response);
              if(response.isConfirmed){
                  history.goBack();
              }
            })
          })
          .catch((error)=>{
            Swal.fire({
              title: 'Error',
              text: 'User has not been deleted, please try again',
              icon: 'error',
              showCancelButton:false,
          });
          })
  }
    return(
        <div className="container py-2" style={{marginTop:"8rem", backgroundColor:"#e2e8f0"}}>
            {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
           {evangelist && <div className="row mx-auto mt-5">
           <div className="col-8 col-md-3 col-lg-3 col-sm-4 mb-3">
           <div className="card">
                <div className="card-body">
                  <div className="d-flex flex-column align-items-center text-center">
                    <img src="https://bootdey.com/img/Content/avatar/avatar7.png" alt="Admin" className="rounded-circle" width="150"/>
                    <div className="mt-3">
                      <h4>{evangelist.first_name}{' '}{evangelist.last_name}</h4>
                      <p className="text-secondary mb-1">Role of the user</p>
                      <p className="text-muted font-size-sm">Evangelist</p>
                      {(evangelist.blocked) ? <button className="btn btn-outline-primary" onClick={handlePrivilege}>Grant Evangelist Privilege</button>:
                      <p>Evangelist of COP</p>}
                    </div>
                  </div>
                </div>
              </div>
           </div>
           <div className="col-8 col-md-7 col-lg-7 col-sm-8 mb-3">
           <div className="card mb-3">
                <div className="card-body">
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">First Name</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.first_name}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Last Name</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.last_name}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Email</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.email}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Status</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.role.name}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Address</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      Bay Area, San Francisco, CA
                    </div>
                  </div>
                  <hr/>
                  {(evangelist.blocked) ? <div className="col-sm-12 text-center" >
                      <button className="btn btn-outline-danger" style={{width:"90%"}} onClick={handleDeleteEvangelist}>Remove this user</button>
                    </div>:
                    <div className="col-sm-12 text-center" >
                      <Link to="/admin/users">
                      <button className="btn btn-outline-info" style={{width:"90%"}}>Go back to user list</button>
                      </Link>
                    </div>}
                    
                  
                </div>
              </div>
           </div>
           </div>}
        </div>
    );
}

export default UserPrivileage;