import axios from "axios";
import { useEffect, useContext, useState } from "react";
import { Context } from '../context/Context'
import { Link } from 'react-router-dom'
import Swal from 'sweetalert2';
import moment from 'moment';


const Events = ()=>{
    const { user, dispatch } = useContext(Context)
    const [data, setData]= useState([]);
    const [error, setError] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const [reload, setReload] = useState(null)
    
    useEffect(()=>{
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
        }

        axios.get("https://evangelism.thecophq.org/api/events", config)
            .then(result=>{
                setData(result.data);
                setIsLoading(false);
                setError(null);
            })
            .catch(err=>{
                setError("An error has been encountered, please try again later");
                setIsLoading(false)
            });
    },[reload])

    const handleDelete = (idAnouncement)=>{
        const config = {
          headers: { Authorization: `Bearer ${user.jwt}` },
      }
  
      axios.delete(`https://evangelism.thecophq.org/api/events/${idAnouncement}`, config)
          .then(result=>{
            // console.log(result.data)
            Swal.fire({
              title: 'Suucessful',
              text: 'Event removed successfully',
              icon: 'success',
              showCancelButton:false,
              timer:2000
            });
            setReload(result.data);
          })
          .catch(err=>{
            Swal.fire({
              title: 'Error',
              text: 'Event was not deleted',
              icon: 'error',
              showCancelButton:false,
              timer:2000
            });
          });
      }
    return(
        <>
             {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {data && <div className="anouncement" style={{marginTop:"6rem"}}>
            <div className="mx-2">
                <Link to={`/admin/edit-event`}> <button  className="btn btn-outline-info">Create Event</button> </Link>
            </div>
            <div className="card p-2 m-2">
                  <div className="card-header">
                    <h3>List of Events</h3>
                  </div>
                  <table className="table table-striped m-1">
                    <thead className="border-top">
                      <tr>
                        <th scope="col">Number</th>
                        
                        <th scope="col">Name</th>
                        <th scope="col">Start Date</th>
                        <th scope="col">End Date</th>
                        <th scope="col">Location</th>
                        <th scope="col">Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {data.map((item,index)=>{
                          return(
                              <tr key={index}>
                                  <td>{index + 1}</td>
                                  <td>{item.name}</td>
                                  <td>{moment(data.start_time).format('YYYY-MM-DD')}</td>
                                  <td>{moment(data.end_time).format('YYYY-MM-DD')}</td>
                                  <td>{item.location.name}</td>
                                  <td>
                                    <Link to={`/admin/event-details/${item.id}`}>
                                  <button
                                    type="button"
                                    className="btn btn-outline-info btn-sm mx-2"
                                    >
                                    Details
                                
                                    </button>
                                    </Link>
                                    
                                    <button
                                    type="button"
                                    className="btn btn-outline-danger btn-sm mx-2"
                                    onClick={()=>{
                                      handleDelete(item.id);
                                    }}
                                    >
                                    Remove
                                    </button>
                                  </td>
                              </tr>
                          );
                      })}
                    </tbody>
                  </table>
                </div>
        </div>}
        </>
        
    );
}

export default Events;