import axios from 'axios';
import moment from 'moment';
import { Container} from "react-bootstrap";
import { useContext, useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router'
import { Context } from '../context/Context'

const EventDetails = ()=>{
    const { user, dispatch } = useContext(Context)
    const { id } = useParams()
    const [prayer, setPrayer] = useState(null)
    const [error, setError] = useState(null)
    const [isLoading, setIsLoading] = useState(true)
    const history = useHistory();

    useEffect(() => {
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
          }
      
          axios
            .get(`https://evangelism.thecophq.org/api/events/${id}`, config)
            .then((result) => {
              setPrayer(result.data)
              // console.log(result.data);
              setError(null)
              setIsLoading(false)
            })
            .catch((err) => {
              // console.log("an error occured");
              setError('An error has been encountered, please try again later')
              setIsLoading(false)
            });
    }, []);
    return(
        <Container className="mt-5">
      {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
      {prayer && (
        <div className="pt-5">
          <div
            className="card pt-2 mx-auto"
            style={{ width: '25rem' }}
          >
            <div className="card-body">
              {/* <h5 className="card-title">Prayer request details</h5> */}
              <div className="py-2 " style={{ backgroundColor: '#f5f4ee' }}>
                <div
                  className="px-3 text-align-start"
                  style={{ display: 'inline-block' }}
                >
                  Event name:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {prayer.name}
                </div>
              </div>
              <div className="py-2" style={{ backgroundColor: '#f9fafa' }}>
                <div
                  className="px-3 text-align-start"
                  style={{ display: 'inline-block' }}
                >
                  Start Date:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {moment(prayer.start_date).format('YYYY-MM-DD')}
                </div>
              </div>
              <div className="py-2" style={{ backgroundColor: '#f5f4ee' }}>
                <div className="px-3 " style={{ display: 'inline-block' }}>
                  End Date:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {moment(prayer.end_date).format('YYYY-MM-DD')}
                </div>
              </div>
              <div className="py-2" style={{ backgroundColor: '#f9fafa' }}>
                <div
                  className="px-3 text-align-start"
                  style={{ display: 'inline-block' }}
                >
                  Location:
                </div>
                <div
                  className="px-3 fw-bold"
                  style={{ display: 'inline-block' }}
                >
                  {prayer.location.name}
                </div>
              </div>
              <hr/>
              <div className="py-2">
                <div className="px-3 pt-3">
                  <p className="card-text">{prayer.description}</p>
                </div>
              </div>
            </div>
            <div className="col-sm-12 text-center" >
                
                    <button className="btn btn-outline-info" style={{width:"90%"}}
                    onClick={()=>{history.goBack()}}
                    >Go back to event list</button>
                
            </div>
            
          </div>
        </div>
      )}
    </Container>
    );
}

export default EventDetails;