import { useRef, useContext, useEffect, useState } from "react";
import { Context } from '../context/Context';
import axios from 'axios';
import { useHistory } from "react-router-dom";
import Swal from 'sweetalert2';


const EventsForm = ()=>{
    const [districts, setDistricts] = useState(null);
    const { user, dispatch } = useContext(Context)
    const nameRef = useRef();
    const locationRef = useRef();
    const dateStartRef = useRef();
    const dateEndRef = useRef();
    const descriptionRef = useRef();
    const history = useHistory();
    // const [selectedDistrict, setSectedDistrict] = useState(null);

    useEffect(()=>{
        getAllDistricts();
    },[])

    const getAllDistricts = ()=>{
        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
          };
      
          axios
            .get(`https://evangelism.thecophq.org/api/districts`, config)
            .then((result) => {
              //   console.log(result.data)
              setDistricts(result.data)
              // setIsLoading(false);
            })
            .catch((err) => {
              setError("An error has been encounted, please try again later");
              setIsLoading(false);
            });
    }

    const registerHandler = (e)=>{
        e.preventDefault();
        const event = {
            name: nameRef.current.value,
            description: descriptionRef.current.value,
            start_time: dateStartRef.current.value,
            end_time: dateEndRef.current.value,
            location: locationRef.current.value
        }

        const config = {
            headers: { Authorization: `Bearer ${user.jwt}` },
          };

          const date1 = new Date(event.start_time);
          const date2 = new Date(event.end_time);

          if(date1.getTime() <= date2.getTime()){
            // console.log(date1.getTime() <= date2.getTime())
            axios
          .post('https://evangelism.thecophq.org/api/events', event, config)
          .then((result) => {
          Swal.fire({
            title: 'Suucessful',
            text: 'Event created successfully',
            icon: 'success',
            showCancelButton:false,
          });
            history.goBack();
          })
          .catch((err) => {
            Swal.fire({
              title: 'Error',
              text: 'Registration failled, try again latter',
              icon: 'error',
              showCancelButton:false,
            });
          })
          }else{
            Swal.fire({
              title: 'Warning',
              text: 'End date must be greater or equal to start date ',
              icon: 'warning',
              showCancelButton:false,
            });
          }
           
    }
    return(
        <div className="edit-anoun" style={{marginTop:"6rem"}}>
            <div className="card mx-auto" style={{ width: "30rem" }}>
                    <div className="card-body ">
                    <h5 className="card-title text-center fw-bold">
                      Events Registration
                    </h5>
                    <form>
                      <label htmlFor="topic" className="form-label">
                        Event Name
                      </label>
                      <input type="text" className="form-control mb-3" id="topic" required ref={nameRef}/>
          
                      <label htmlFor="date" className="form-label">
                        Start Date
                      </label>
                      <input type="date" className="form-control mb-3" id="date" required ref={dateStartRef} />
                      <label htmlFor="date" className="form-label">
                        End Date
                      </label>
                      <input type="date" className="form-control mb-3" id="date" required ref={dateEndRef} />

                      <label htmlFor="topic" className="form-label">
                        Location
                      </label>
                      <select className="form-select form-select-lg mb-3"  ref={locationRef}>
                        {/* <option>Select Location</option> */}
                            {districts ? <>
                                {districts.map((district, index)=>{
                                    return(
                                        <option key={index}
                                            value={district.id}
                                        >
                                                {district.name}
                                        </option>
                                    );
                                })}
                            </>:<option>No Data</option>}
                        </select>
                      <label htmlFor="conclusion" className="form-label">
                        Description
                      </label>
                      <textarea
                        className="form-control mb-3"
                        id="conclusion"
                        required
                        ref={descriptionRef}
                      ></textarea>
                      <div>
                      <button className="btn btn-info text-light" style={{width:"100%"}} onClick={registerHandler} >Register</button>
                      </div>
                    </form>
                  </div>
                  
            </div>
        </div>
    );
}

export default EventsForm;